document.addEventListener('DOMContentLoaded', () => {
    seleccionarProducto();
    opciones();
    crearLista();
})

// variables 
let seleccion = false;

function seleccionarProducto() {
    const productos = document.querySelectorAll('.producto');
    
    productos.forEach( producto => {
        producto.addEventListener('click', (e) => {
            
            // evalúa si se ha seleccionado un elemento para añadirlo a la lista
            if ( (producto.children)[2].disabled == false & e.target != (producto.children)[2] ) {
                producto.classList.remove('seleccion'); // se remueve la clase de selección
                (producto.children)[2].disabled = true; // no se permite interactuar sobre el input
                (producto.children)[2].value = 0; // resetear valores del input "cantidad"
            } else {
                producto.classList.add('seleccion'); // se añade la clase de selección
                (producto.children)[2].disabled = false; // se permite interactuar sobre el input
                (producto.children)[2].value = ''; // coloca el campo vacío
                seleccion = true;
            }
           
        })
    });
}

function crearLista() {

    const productos = document.querySelectorAll('.producto');
    const nombreLista = document.querySelector('.nombre-lista');
    const campo = document.querySelector('.campo-vacio')
    const productoError = document.querySelector('.lista-vacia');
    const submit = document.querySelector('.crear-lista');

    submit.addEventListener('click', (e) => {

        // evalúa si el campo está vacío
        if ( nombreLista.value == '') {
            nombreLista.classList.add('campo-error'); // resalta el campo en rojo
            campo.style.display = 'block'; // genera el mensaje de error
            window.scrollTo(0, 0);
            e.preventDefault();

        } else {
            nombreLista.classList.remove('campo-error');
            campo.style.display = 'none';

            // evalúa si se ha seleccionado un elemento (producto) para enviar el formulario
            if ( seleccion ) {
                document.querySelector('.formulario').submit();
            } else {
                productoError.style.display = 'block';
                window.scrollTo(0, 0);
                e.preventDefault();
            }

        }
    })
}

function opciones() {
    const opcs = document.querySelectorAll('.opciones');

    opcs.forEach(opcion => {

        opcion.children[1].addEventListener('click', (e) => {
            
            // evalúa dentro del primer elemento del div que el segundo subelemento (botón de borrar) tenga o no la clase 'block' para reescribir su display

            if ((opcion.children[0]).children[1].classList.contains('block')) {
                e.preventDefault();
                (opcion.children[0]).children[1].classList.remove('block');
                opcion.children[1].style.color = '#b3b3b3';

            } else {
                e.preventDefault();
                (opcion.children[0]).children[1].classList.add('block');
                opcion.children[1].style.color = '#333';

                // evalúa sí el botón de borrar ha sido pulsado para enviar datos de formulario
                (opcion.children[0]).addEventListener('click', (e) => {
                    e.preventDefault();
                    opcion.children[0].submit(); // segundo elemento del div (formulario)
                });
            }

        })
    });
}